import { Suspense, lazy } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { ClickSparkles } from './components/common/ClickSparkles';

// Lazy load pages to reduce initial bundle size
const Home = lazy(() => import('./pages/Home'));
const ProjectDetailPage = lazy(() => import('./pages/ProjectDetailPage'));

// Loading component for Suspense
const LoadingFallback = () => (
  <div className="flex justify-center items-center h-screen bg-macaron-cream">
    <div className="animate-spin rounded-full h-12 w-12 border-4 border-macaron-pink border-t-macaron-blue"></div>
  </div>
);

function App() {
  return (
    <Router basename={import.meta.env.BASE_URL}>
      <ClickSparkles />
      <Suspense fallback={<LoadingFallback />}>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/project/:id" element={<ProjectDetailPage />} />
        </Routes>
      </Suspense>
    </Router>
  );
}

export default App;
